'use client';

import { useEffect, useState } from 'react';
import { DndContext, closestCorners, DragEndEvent } from '@dnd-kit/core';
import { SortableContext, horizontalListSortingStrategy, arrayMove } from '@dnd-kit/sortable';
import { List as ListType, Card as CardType, Board as BoardType } from '@/types';
import { List } from '@/components/dnd/List';

export default function Home() {
  const [board, setBoard] = useState<BoardType | null>(null);
  const [lists, setLists] = useState<ListType[]>([]);
  const [cards, setCards] = useState<CardType[]>([]);

  useEffect(() => {
    // TODO: Replace with a dynamic way to get the board ID
    const boardId = '66779308182b02234a532382'; // IMPORTANT: Replace with a valid board ID from your DB
    if (boardId) {
      fetch(`/api/boards/${boardId}`)
        .then(res => res.json())
        .then(data => {
          setBoard(data.board);
          setLists(data.lists);
          setCards(data.cards);
        });
    }
  }, []);

  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;
    if (!over) return;

    if (active.id !== over.id) {
      // Dragging cards
      if (active.data.current?.type === 'Card') {
        setCards((cards) => {
          const activeIndex = cards.findIndex(c => c._id === active.id);
          const overIndex = cards.findIndex(c => c._id === over.id);
          if (activeIndex === -1 || overIndex === -1) return cards;
          let newCards = arrayMove(cards, activeIndex, overIndex);

          // Update list if card is moved to a different list
          const overListId = over.data.current?.sortable?.containerId || cards[overIndex].list;
          if (overListId && cards[activeIndex].list !== overListId) {
             newCards = newCards.map((c, index) => index === overIndex ? {...c, list: overListId} : c)
          }

          // API call to update card position and list
          fetch(`/api/cards/${active.id}`, {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
              position: overIndex,
              list: overListId,
            }),
          });
          
          return newCards;
        });
      }

      // Dragging lists
      if (active.data.current?.type === 'List') {
        setLists((lists) => {
          const activeIndex = lists.findIndex(l => l._id === active.id);
          const overIndex = lists.findIndex(l => l._id === over.id);
          if (activeIndex === -1 || overIndex === -1) return lists;
          const newLists = arrayMove(lists, activeIndex, overIndex);

          // API call to update list position
          newLists.forEach((list, index) => {
            fetch(`/api/lists/${list._id}`, {
                method: 'PUT',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ position: index }),
            });
          });

          return newLists;
        });
      }
    }
  };

  if (!board) {
    return (
        <div className="flex justify-center items-center h-screen">
            <p>Please update the boardId in <code>src/app/page.tsx</code> with an ID from your database.</p>
        </div>
    );
  }

  return (
    <DndContext onDragEnd={handleDragEnd} collisionDetection={closestCorners}>
      <div className="p-4">
        <h1 className="text-2xl font-bold mb-4">{board.name}</h1>
        <SortableContext items={lists.map(l => l._id)} strategy={horizontalListSortingStrategy}>
          <div className="flex gap-4">
            {lists.map(list => (
              <List key={list._id} id={list._id} list={list} cards={cards.filter(c => c.list === list._id)} />
            ))}
          </div>
        </SortableContext>
      </div>
    </DndContext>
  );
}